// editor.scss


// Define custom styles for the WordPress editor
body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
    margin-top: 1.5em;
    margin-bottom: 0.5em;
}

p {
    margin-bottom: 1em;
}

a {
    color: #0073aa;
    text-decoration: none;

    &:hover {
        color: #005177;
        text-decoration: underline;
    }
}

ul, ol {
    margin: 1em 0;
    padding-left: 2em;
}

blockquote {
    margin: 1em 0;
    padding: 0.5em 1em;
    background-color: #f9f9f9;
    border-left: 4px solid #ccc;
}

code {
    font-family: 'Courier New', Courier, monospace;
    background-color: #f4f4f4;
    padding: 0.2em 0.4em;
    border-radius: 3px;
}

pre {
    background-color: #f4f4f4;
    padding: 1em;
    overflow-x: auto;
    border-radius: 3px;
}

img {
    max-width: 100%;
    height: auto;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1em;

    th, td {
        padding: 0.5em;
        border: 1px solid #ddd;
    }

    th {
        background-color: #f4f4f4;
    }
}